import React from 'react';
// Types
interface IIconSkeletonProps {
  cssClasses?: {
    loaderColor?: string;
    backgroundColor?: string;
  };
}

const IconSkeleton: React.FC<IIconSkeletonProps> = ({ cssClasses }) => {
  return (
    <svg
      width="1200"
      height="600"
      viewBox="0 0 1200 600"
      xmlns="http://www.w3.org/2000/svg"
    >
      <defs>
        <linearGradient id="skeleton">
          <stop
            offset="0%"
            stopColor="#fff"
            className={cssClasses?.backgroundColor}
          />
          <stop
            offset="0%"
            stopColor="#000"
            className={cssClasses?.loaderColor}
          >
            <animate
              attributeName="offset"
              from="0"
              to="2"
              dur="2s"
              repeatCount="indefinite"
              fill="freeze"
            />
          </stop>
          <stop
            offset="100%"
            stopColor="#fff"
            className={cssClasses?.backgroundColor}
          />
        </linearGradient>
      </defs>
      <rect
        x="0"
        y="0"
        rx="0"
        ry="0"
        width="1200"
        height="600"
        fill="url(#skeleton)"
      />
    </svg>
  );
};

export default IconSkeleton;
