import React, { useContext } from 'react';
// React Context
import { sectionContext } from '../../../../context/sectionContext';
// Utils
import { convertInString } from '../../../../utils/js/сonvert';
import { activeColor } from '../../../../utils/ts/activeColor';
// Components
import UsTitle from '../../../Common/UsTitle';
import ContactLink from './ContactLink';
// Styles-module
import contactList from './contactList.module.scss';
const box = (value: string) =>
  convertInString(
    contactList[`${value}__box`],
    ...activeColor('borderColor-1', 'gradient', 'boxShadow-1')
  );

const ContactList: React.FC = () => {
  // React Context
  const { lang, data } = useContext(sectionContext);

  return (
    <ul className={contactList.container}>
      {(data as DataContacts).map(({ title, group, data }, i) => (
        <li key={`contact-box-${i + 1}`} className={box(group)}>
          <UsTitle
            level={2}
            cssClass={contactList[`${group}__title`]}
            content={title[lang.code]}
          />
          <ul className={contactList[`${group}__list`]}>
            {data.map((obj, i) => (
              <li
                key={`contact-item-${i + 1}`}
                className={contactList[`${group}__item`]}
                children={<ContactLink group={group} {...obj} />}
              />
            ))}
          </ul>
        </li>
      ))}
    </ul>
  );
};

export default ContactList;
