import React, {
  Suspense,
  useContext,
  useEffect,
  useRef,
  useState,
} from 'react';
// React Context
import { sectionContext } from '../../../context/sectionContext';
// Utils
import { convertInString } from '../../../utils/js/сonvert';
import { activeColor } from '../../../utils/ts/activeColor';
// Components
import Blockquote from '../../Common/Blockquote';
import UsTitle from '../../Common/UsTitle';
import ContactList from './ContactList';
import InputList from './InputList';
import BtnSend from './BtnSend';
import IconSkeleton from '../../Common/Icon/IconSkeleton';
// Styles-module
import contacts from './contacts.module.scss';
const form = convertInString(
  contacts.form,
  ...activeColor('borderColor-1', 'gradient', 'boxShadow-1')
);
// Components -- Lazy
const YandexMap = React.lazy(() => import('./YandexMap'));
const Skeleton = (
  <IconSkeleton
    cssClasses={{
      backgroundColor: 'active-stopColor-2',
      loaderColor: 'active-stopColor-1',
    }}
  />
);

const Contacts: React.FC = () => {
  // React Context
  const { lang, animateTime } = useContext(sectionContext);
  // React State
  const [mapTimeout, setMapTimeout] = useState<boolean>(true);
  // React Ref
  const formRef = useRef<HTMLFormElement>(null);
  // React Effect
  useEffect(() => {
    setTimeout(() => setMapTimeout(false), animateTime.init);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      <Blockquote
        blockquote={
          lang.bool
            ? 'Извлекайте уроки из вчерашнего дня, живите сегодняшним днем, надейтесь на завтрашний. Главное — не переставать задавать вопросы'
            : 'Learn from yesterday, live for today, hope for tomorrow. The important thing is not to stop questioning'
        }
        author={lang.bool ? 'Альберт Эйнштейн' : 'Albert Einstein'}
        cssClasses={{
          box: contacts.blockquote__box,
        }}
      />
      <ContactList />
      <div className={contacts.container}>
        <form ref={formRef} className={form}>
          <UsTitle
            level={2}
            cssClass={contacts.form__title}
            content={lang.bool ? 'Напишите мне' : 'Write me'}
          />
          <InputList />
          <BtnSend form={formRef.current} />
        </form>
        <div className={contacts.map__box}>
          {mapTimeout ? (
            Skeleton
          ) : (
            <Suspense fallback={Skeleton} children={<YandexMap />} />
          )}
        </div>
      </div>
    </>
  );
};

export default Contacts;
