import React, { useContext } from 'react';
// React Context
import { sectionContext } from '../../../../context/sectionContext';
// Utils
import { convertInString } from '../../../../utils/js/сonvert';
import { activeColor } from '../../../../utils/ts/activeColor';
// Styles-module
import inputList from './inputList.module.scss';
const formDesc = convertInString(
  inputList.desc,
  ...activeColor('color-1', 'textShadow-1')
);
const formInput = convertInString(
  inputList.input,
  ...activeColor('borderColor-2', 'background-1', 'background-focus')
);
// Types
enum EInputMode {
  email = 'email',
  tel = 'tel',
}

const InputList: React.FC = () => {
  // React Context
  const { lang } = useContext(sectionContext);

  return (
    <div className={inputList.box__input}>
      <p className={formDesc}>
        {lang.bool
          ? 'Поля, отмеченные звездочкой, являются обязательными!'
          : 'Fields marked with an asterisk are required!'}
      </p>
      <ul className={inputList.main}>
        {[
          {
            type: 'text',
            name: 'name',
            lang: lang.code,
            minLength: 2,
            maxLength: 20,
            placeholder: lang.bool ? 'Имя*' : 'Name*',
            required: true,
            'data-excep': '-',
            autoComplete: 'given-name',
          },
          {
            type: EInputMode.email,
            name: EInputMode.email,
            inputMode: EInputMode.email,
            placeholder: lang.bool ? 'Электронная почта*' : 'E-mail*',
            required: true,
            autoComplete: EInputMode.email,
          },
          {
            type: EInputMode.tel,
            name: EInputMode.tel,
            inputMode: EInputMode.tel,
            placeholder: lang.bool ? 'Номер телефона' : 'Phone number',
            autoComplete: EInputMode.tel,
          },
        ].map((o) => (
          <li
            key={`contact-${o.name}`}
            className={inputList.item}
            children={
              <>
                <label
                  className={inputList.label}
                  htmlFor={`input-${o.name}`}
                  children={`${o.placeholder.replace(/[*]/g, '')}`}
                />
                <input className={formInput} id={`input-${o.name}`} {...o} />
              </>
            }
          />
        ))}
        <li
          className={inputList.item}
          children={
            <>
              <label
                className={inputList.label}
                htmlFor={'textarea-message'}
                children={lang.bool ? 'Комментарий' : 'Сomment'}
              />
              <textarea
                className={formInput}
                id={'textarea-message'}
                name="message"
                placeholder={lang.bool ? 'Комментарий' : 'Сomment'}
              />
            </>
          }
        />
      </ul>
    </div>
  );
};

export default InputList;
