import React from 'react';
// Utils
import { convertInString } from '../../../../../utils/js/сonvert';
import { activeColor } from '../../../../../utils/ts/activeColor';
// Components
import Icon from '../../../../Common/Icon';
// Styles-module
import contactLink from './contactLink.module.scss';
const link = () =>
  convertInString(contactLink.link, ...activeColor('color-1', 'color-focus'));
// Types
interface IContactLinkProps extends IContactData {
  group: string;
}

const ContactLink: React.FC<IContactLinkProps> = ({ group, ...valueLink }) => {
  return (
    <a className={link()} {...valueLink} translate="no" lang="en">
      <Icon
        active={valueLink.name}
        size={50}
        cssClass={{
          iconColor: 'active-fill-1',
          backgroundColor: 'active-fill-2',
        }}
      />
      <span className={contactLink.link__desc}>
        {group === 'social' ? valueLink.name : valueLink.value}
      </span>
    </a>
  );
};

export default ContactLink;
