import React, { useContext, useRef, useState } from 'react';
// React Context
import { sectionContext } from '../../../../context/sectionContext';
// Utils
import { convertInString } from '../../../../utils/js/сonvert';
import { activeColor } from '../../../../utils/ts/activeColor';
// Components
import IconPreloader from '../../../Common/Icon/IconPreloader';
// Styles-module
import btnSend from './btnSend.module.scss';
const formBtn = convertInString(
  btnSend.main,
  ...activeColor('borderColor-1', 'background-3', 'background-focus')
);
const formMessage = convertInString(btnSend.message, 'active-color-1');

const BtnSend: React.FC<{ form: HTMLFormElement | null }> = ({ form }) => {
  // .env
  const env = process.env;
  // React Context
  const { lang } = useContext(sectionContext);
  // React State
  const [loading, setLoading] = useState<boolean>(false);
  const [message, setMessage] = useState<string>('');
  // React Ref
  const messageRef = useRef<HTMLParagraphElement>(null);
  // Submit Form
  const submitForm = async () => {
    // Get data from a form
    const data = (await import('../../../../utils/js/dataform')).default(
      form,
      lang.code
    );

    if (data !== null) {
      setLoading(true);

      try {
        const response = await fetch(env.REACT_APP__MESSAGE as string, {
          method: 'POST',
          body: JSON.stringify({ path: env.PUBLIC_URL, ...data }),
          headers: {
            'Content-Type': 'application/json',
          },
        });

        if (response.status >= 200 && response.status <= 299)
          return setMessage(
            lang.bool
              ? 'Данные успешно отправлены, я свяжусь с вами в ближайшее время!'
              : 'Data sent successfully, I will contact you shortly!'
          );

        throw new Error(
          lang.bool
            ? 'Произошла ошибка. Пожалуйста, повторите попытку позже!'
            : 'An error occurred, please try again later!'
        );
      } catch (error) {
        error instanceof Error && setMessage(error.message);
      } finally {
        const messageEl = messageRef.current?.classList;

        setLoading(false);

        messageEl?.add(btnSend.message__active);
        setTimeout(() => {
          messageEl?.remove(btnSend.message__active);
          setTimeout(() => setMessage(''), 400);
        }, 5000);
      }
    }
  };

  return (
    <div className={btnSend.box}>
      <button type="button" className={formBtn} onClick={() => submitForm()}>
        {loading && <IconPreloader cssClass="active-stroke-1" />}
        <span
          className="active-color-effect"
          children={`${lang.bool ? 'Отправить' : 'Send'}`}
        />
      </button>
      <p
        ref={messageRef}
        className={formMessage}
        role="alert"
        children={message}
      />
    </div>
  );
};

export default BtnSend;
